.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		@include max-md {
			.container {
				padding: 0;
			}
		}
	}
	.story-content {
		position: relative;
		background-color: var(--color-primary);
		padding: 3rem;
		border-radius: 300px 20px 20px 300px;
		@include max-lg {
			border-radius: 500px 500px 20px 20px;
		}
		@include max-md {
			padding: 2rem;
		}
		.no-photos {
			border-radius: 20px;
			.story-description {
				padding-left: 0;
			}
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		padding-left: 2rem;
		@include max-lg {
			padding-left: 0;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		@include max-lg {
			margin-bottom: 2rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 500px;
			}
		}
	}
}
