.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
			@include max-md {
				font-size: 28px;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		padding: 5rem 2rem 3rem;
		margin-bottom: 3rem;
		background-color: var(--color-primary);
		border-radius: 500px 500px 20px 20px;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
		p {
			margin-bottom: 0;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		padding: 2rem;
		border-radius: 500px 500px 20px 20px;
		box-shadow: inset 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px 500px 20px 20px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
